import VisualComponent from "jx/core/comps/VisualComponent";
import Common from "jx/editor/editionitems/Common";
let textInput = `
<div class="form-group row">
  <div class="row">
    <div class="col-xs-5">
        <div class="input-group">
          <span id="check" class="input-group-addon"><span class="fa fa-cog"></span></span>
     <input type="text" class="form-control" id="textSound" value="ID" readonly="true" >
        </div>
    </div>
    <div class="col-xs-1">
         <button class="btn btn-default" title="Informations" id="info"><span class="fa fa-info"></span></button>
    </div>
    <div class="col-xs-6">
      <input  id="value" type="number"  class="form-control"  value="$VALUE">
   </div>
  </div>
  <div  id="informationsPanel" class="row"  style="display:none;">
    <div class="col-xs-12" id="informationsText">
      <div id="description">$description</div>
    </div>
  </div>
</div>
`;
export default class Number extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        this.commonLib = new Common({ jx: this.jx });
        var item = $(textInput);
        item.prop('id', this.data.id);
        //if (this.data.description == undefined) { item.remove($(item.description_btn)); }
        item.find("#description").html("<pre>" + this.data.description + "</pre>" || "");
        item.find("#value").prop('readonly', (this.data.editable != undefined) ? !this.data.editable : false);
        item.find("#value").attr('min', this.data.min);
        item.find("#value").attr('max', this.data.max);
        item.find("#value").attr('step', this.data.step);
        item[0].addEventListener("change", (event) => { this.controller.onChangeData(event); });
        item.html(item[0].innerHTML.replace("ID", this.data.id).replace("$VALUE", this.data.value || ""));
        item.find("#info").click((event) => {
            console.log("info", event.target, item[0].id);
            var panelInfo = item.find("#informationsPanel");
            panelInfo.css("display", (panelInfo.css("display") == "none" ? "inline" : "none"));
        });
        this.item = item;
    }
    set value(itemValue) {
        this.item.find("#value").attr('value', itemValue);
    }
    get value() {
        return this.item.find("#value").attr('value');
    }
}
